.logo-svg {
  fill: #fff;
}

.logo-svg .line {
  transform: scaleY(0);
}

.logo-svg:hover .rest-of-logo {
  animation: go-down .7s ease-in-out forwards;
}

.logo-svg:hover .line:nth-of-type(1) {
  animation: rise .5s ease-in-out .2s forwards;
}

.logo-svg:hover .line:nth-of-type(2) {
  animation: rise .5s ease-in-out .3s forwards;
}

.logo-svg:hover .line:nth-of-type(3) {
  animation: rise .5s ease-in-out .4s forwards;
}

.logo-svg-mobile:hover, .logo-svg-desktop:hover {
  height: 100px;
  transition: all .3s;
}

@keyframes go-down {
  to {
    transform: translateY(40px);
  }
}

@keyframes rise {
  to {
    transform: scaleY(1);
  }
}

